"use client";
import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import { requireAdminAuth } from "@/lib/adminAuth";
import { useLanguage } from "@/components/LanguageProvider";
import {
  getAllCategories,
  deleteCategory,
  getCategoryNameInLanguage,
} from "@/lib/categoriesApi";
import DashboardHeader from "@/components/admin/dashboard/DashboardHeader";
import DashboardSidebar from "@/components/admin/dashboard/DashboardSidebar";

export default function CategoriesPage() {
  const router = useRouter();
  const [categories, setCategories] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);
  const { t, lang } = useLanguage();

  useEffect(() => {
    const checkAuth = async () => {
      if (!requireAdminAuth(router)) {
        return;
      }
      loadCategories();
    };

    checkAuth();
  }, [router]);

  // Reload categories when page is focused (e.g., after creating a category)
  useEffect(() => {
    const handleFocus = () => {
      if (!isLoading) {
        loadCategories();
      }
    };

    window.addEventListener("focus", handleFocus);
    return () => window.removeEventListener("focus", handleFocus);
  }, [isLoading]);

  const loadCategories = async () => {
    try {
      setIsLoading(true);
      setError(null);
      console.log("Starting to load categories...");

      const data = await getAllCategories();
      console.log("Categories loaded successfully:", data);

      // Ensure data is an array
      if (Array.isArray(data)) {
        setCategories(data);
      } else {
        console.warn("Categories data is not an array:", data);
        setCategories([]);
      }
    } catch (error) {
      console.error("Error loading categories:", error);
      console.error("Error details:", {
        message: error.message,
        stack: error.stack,
        name: error.name,
      });
      setError(error.message);
      setCategories([]); // Set empty array on error
    } finally {
      setIsLoading(false);
    }
  };

  const handleCreateCategory = () => {
    router.push("/admin/categories/create");
  };

  const handleEditCategory = (categoryId) => {
    router.push(`/admin/categories/edit/${categoryId}`);
  };

  const handleDeleteCategory = async (categoryId) => {
    if (!confirm(t("categories.confirmDelete"))) {
      return;
    }

    try {
      await deleteCategory(categoryId);
      await loadCategories(); // Reload the list
    } catch (error) {
      console.error("Error deleting category:", error);
      setError(error.message);
    }
  };

  const handleLogout = async () => {
    try {
      const { authAPI } = await import("@/lib/api");
      await authAPI.logout();
      router.push("/admin/login");
    } catch (error) {
      console.error("Logout error:", error);
    }
  };

  if (isLoading) {
    return (
      <div
        className={`min-h-screen bg-gray-50 ${lang === "ar" ? "rtl" : "ltr"}`}
      >
        <DashboardHeader user={null} onLogout={handleLogout} />
        <div className="flex main-layout">
          <div className={lang === "ar" ? "order-2" : "order-1"}>
            <DashboardSidebar />
          </div>
          <main
            className={`flex-1 py-6 px-4 sm:px-6 lg:px-8 ${
              lang === "ar" ? "order-1" : "order-2"
            }`}
          >
            <div className="flex items-center justify-center h-64">
              <div className="text-center">
                <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto"></div>
                <p className="mt-4 text-gray-600">{t("categories.loading")}</p>
              </div>
            </div>
          </main>
        </div>
      </div>
    );
  }

  return (
    <div className={`min-h-screen bg-gray-50 ${lang === "ar" ? "rtl" : "ltr"}`}>
      <DashboardHeader user={null} onLogout={handleLogout} />

      <div className="flex main-layout">
        <div className={lang === "ar" ? "order-2" : "order-1"}>
          <DashboardSidebar />
        </div>
        <main
          className={`flex-1 py-6 px-4 sm:px-6 lg:px-8 ${
            lang === "ar" ? "order-1" : "order-2"
          }`}
        >
          <div className="px-4 py-6 sm:px-0">
            {/* Debug info */}
            <div className="mb-4 p-2 bg-yellow-100 text-xs">
              Language: {lang} | Layout: {lang === "ar" ? "RTL" : "LTR"}
            </div>
            {/* Header */}
            <div className="flex justify-between items-center mb-6">
              <div
                className={`flex items-center ${
                  lang === "ar" ? "order-2" : "order-1"
                }`}
              >
                <h1
                  className={`text-2xl font-bold text-gray-900 ${
                    lang === "ar" ? "text-right" : "text-left"
                  }`}
                >
                  {t("categories.title")}
                </h1>
              </div>
              <div
                className={`flex items-center ${
                  lang === "ar" ? "order-1" : "order-2"
                }`}
              >
                <button
                  onClick={handleCreateCategory}
                  className="admin-button admin-button-primary"
                >
                  {t("categories.create")}
                </button>
              </div>
            </div>

            {/* Error Message */}
            {error && (
              <div className="mb-4 p-4 bg-red-100 border border-red-400 text-red-700 rounded">
                {error}
              </div>
            )}

            {/* Categories Table */}
            <div className="bg-white shadow overflow-hidden sm:rounded-md">
              {categories.length === 0 ? (
                <div className="text-center py-12">
                  <p className="text-gray-500">
                    {t("categories.noCategories")}
                  </p>
                </div>
              ) : (
                <div className="overflow-x-auto">
                  <table className="min-w-full divide-y divide-gray-200">
                    <thead className="bg-gray-50">
                      <tr>
                        {lang === "ar" ? (
                          <>
                            <th className="px-6 py-3 text-xs font-medium text-gray-500 uppercase tracking-wider text-right">
                              {t("categories.actions")}
                            </th>
                            <th className="px-6 py-3 text-xs font-medium text-gray-500 uppercase tracking-wider text-right">
                              {t("categories.translations")}
                            </th>
                            <th className="px-6 py-3 text-xs font-medium text-gray-500 uppercase tracking-wider text-right">
                              {t("categories.name")}
                            </th>
                          </>
                        ) : (
                          <>
                            <th className="px-6 py-3 text-xs font-medium text-gray-500 uppercase tracking-wider text-left">
                              {t("categories.name")}
                            </th>
                            <th className="px-6 py-3 text-xs font-medium text-gray-500 uppercase tracking-wider text-left">
                              {t("categories.translations")}
                            </th>
                            <th className="px-6 py-3 text-xs font-medium text-gray-500 uppercase tracking-wider text-left">
                              {t("categories.actions")}
                            </th>
                          </>
                        )}
                      </tr>
                    </thead>
                    <tbody className="bg-white divide-y divide-gray-200">
                      {categories.map((category) => (
                        <tr key={category.Id} className="hover:bg-gray-50">
                          <td
                            className={`px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900 ${
                              lang === "ar" ? "text-right" : "text-left"
                            }`}
                          >
                            {getCategoryNameInLanguage(category, lang)}
                          </td>
                          <td
                            className={`px-6 py-4 whitespace-nowrap text-sm text-gray-500 ${
                              lang === "ar" ? "text-right" : "text-left"
                            }`}
                          >
                            <div className="flex flex-wrap gap-2">
                              {category.Translations?.map((translation) => (
                                <span
                                  key={translation.Id}
                                  className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-blue-100 text-blue-800"
                                >
                                  {translation.LanguageName}: {translation.Name}
                                </span>
                              ))}
                            </div>
                          </td>
                          <td
                            className={`px-6 py-4 whitespace-nowrap text-sm font-medium ${
                              lang === "ar" ? "text-right" : "text-left"
                            }`}
                          >
                            <div className="flex space-x-2">
                              <button
                                onClick={() => handleEditCategory(category.Id)}
                                className="text-indigo-600 hover:text-indigo-900"
                              >
                                {t("categories.edit")}
                              </button>
                              <button
                                onClick={() =>
                                  handleDeleteCategory(category.Id)
                                }
                                className="text-red-600 hover:text-red-900"
                              >
                                {t("categories.delete")}
                              </button>
                            </div>
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </div>
              )}
            </div>

            {/* Back to Dashboard Button */}
            <div className="mt-6 flex justify-center">
              <button
                onClick={() => router.push("/admin/dashboard")}
                className="admin-button admin-button-secondary"
              >
                {t("categories.backToDashboard")}
              </button>
            </div>
          </div>
        </main>
      </div>
    </div>
  );
}
